/* General About Section Styling */
.about-section {
  padding: 2rem;
  background-color: #121212; /* Dark background */
  color: #fff; /* White text for better readability */
}

/* Section Heading */
.about-section h2 {
  font-size: 2.5rem; /* Larger font for the title */
  color: #ffc107; /* Yellow title color */
  text-align: center; /* Center-align the title */
  margin-bottom: 1.5rem;
}

/* About Intro */
.about-intro {
  text-align: center;
  color: #eaeaea; /* Softer white for intro text */
  margin-bottom: 1.5rem;
}

.intro-box {
  padding: 1.5rem;
  background-color: #1a1a1a; /* Subtle background for intro box */
  border-radius: 10px; /* Rounded corners */
  color: #ffc107; /* Contrast for text inside the box */
  margin-bottom: 1.5rem;
}

/* Skill Tags */
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px; /* Space between skill tags */
  margin-bottom: 1.5rem;
}

.skill-tag {
  margin: 0.5rem;
  font-size: 1rem; /* Standard font size */
  background-color: #ffc107; /* Yellow background for skill tags */
  color: #121212; /* Text color for contrast */
  border-radius: 5px; /* Rounded corners for styling */
  padding: 6px 12px; /* Add padding for better appearance */
  font-weight: bold; /* Emphasize skill text */
}

/* Journey Tabs */
/* Journey Tabs */
.journey-tabs {
  display: flex;
  justify-content: space-evenly; /* Space buttons evenly */
  align-items: center;
  gap: 15px;
  margin-top: 1rem; /* Add spacing from the content above */
}

.journey-tabs button {
  background-color: #ffc107; /* Button color */
  color: #121212; /* Text color */
  border: none;
  padding: 10px 15px; /* Adjust padding for a balanced look */
  font-size: 1rem; /* Standard font size */
  font-weight: bold;
  border-radius: 5px; /* Rounded corners */
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  flex: 1; /* Equal width for all buttons */
  max-width: 120px; /* Limit button width for uniformity */
  height: 40px; /* Ensure consistent button height */
  text-align: center; /* Center align text */
  display: flex;
  justify-content: center; /* Center text and icons */
  align-items: center;
}

.journey-tabs button:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
  background-color: #ffdd57; /* Lighter yellow on hover */
}

/* Timeline Container */
.timeline-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem; /* Space between timeline items */
  background-color: #1a1a1a; /* Dark background for timeline */
  padding: 1.5rem;
  border-radius: 10px; /* Rounded corners */
  margin-top: 2rem; /* Add spacing from other sections */
}

.timeline-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.timeline-item .circle {
  width: 15px;
  height: 15px;
  background-color: #ffc107; /* Yellow for timeline dot */
  border-radius: 50%;
  margin-bottom: 10px;
}

.timeline-item .content h4 {
  font-size: 1.2rem;
  color: #ffc107; /* Yellow color for headings */
  margin-bottom: 0.5rem;
}

.timeline-item span {
  font-size: 0.9rem;
  color: #eaeaea; /* Softer white for details */
}

/* Responsive Styles */

/* For medium screens */
@media (max-width: 768px) {
  .about-section {
    padding: 1rem;
  }

  .about-section h2 {
    font-size: 2rem;
  }

  .skills {
    gap: 5px;
  }

  .skill-tag {
    font-size: 0.9rem;
    padding: 5px 10px;
  }

  .journey-tabs {
    gap: 10px;
  }

  .journey-tabs button {
    font-size: 0.9rem;
    padding: 8px 10px;
    max-width: 120px;
  }

  .timeline-container {
    padding: 1rem;
    gap: 1rem;
  }

  .timeline-item .content h4 {
    font-size: 1.1rem;
  }

  .timeline-item span {
    font-size: 0.8rem;
  }
}

/* For small screens */
@media (max-width: 480px) {
  .about-section h2 {
    font-size: 1.8rem;
  }

  .skills {
    flex-direction: column; /* Stack skill tags */
    gap: 5px;
  }

  .skill-tag {
    font-size: 0.8rem;
    padding: 4px 8px;
  }

  .journey-tabs {
    flex-wrap: wrap; /* Allow wrapping if needed */
    gap: 8px;
  }

  .journey-tabs button {
    font-size: 0.8rem; /* Reduce font size */
    max-width: 100px; /* Smaller button width */
    height: 35px; /* Adjust height */
    padding: 8px; /* Adjust padding */
  }

  .timeline-container {
    padding: 0.8rem;
  }

  .timeline-item .content h4 {
    font-size: 1rem;
  }

  .timeline-item span {
    font-size: 0.7rem;
  }
}
