/* Section Container */
.section-container {
  margin: 40px auto;
  padding: 20px;
  max-width: 1200px; /* Centralize the content and limit max width */
  text-align: left;
}

.section-container .content-header p {
  font-size: 22px;
}

.section-container .content-header h2 {
  font-size: 42px;
}

/* Header Styling */
.content-header {
  margin-bottom: 20px;
}

.content-header h2 {
  font-size: 32px; /* Larger title font size for better emphasis */
  margin-bottom: 15px;
  color: #ffc107; /* Title color */
  font-weight: bold; /* Emphasize title */
}

.content-header p {
  font-size: 20px; /* Optimal description font size for readability */
  line-height: 1.8; /* Adequate spacing for easier reading */
  color: #eaeaea; /* Softer white for less strain on the eyes */
  margin-bottom: 20px; /* Add spacing after description for better layout */
}

/* Main Image Section */
.main-image-container {
  text-align: center;
  margin: 20px 0;
}

.main-image {
  width: 100%;
  max-width: 400px; /* Increased size for larger displays */
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  margin: 0 auto; /* Center the image */
}

.main-image:hover {
  transform: scale(1.05); /* Subtle zoom on hover */
}

/* Gallery Section */
.gallery-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Wider columns for better visibility */
  gap: 20px; /* Increased spacing between images */
  margin-top: 20px;
}

.gallery-image {
  width: 100%;
  max-height: 250px; /* Slightly larger height for better visibility */
  object-fit: cover; /* Crop images for consistent sizes */
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s;
}

.gallery-image:hover {
  transform: scale(1.05);
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .gallery-container {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* Adjust grid for smaller screens */
  }

  .content-header h2 {
    font-size: 1.8rem;
  }

  .content-header p {
    font-size: 18px; /* Slightly smaller font for smaller screens */
  }
}

@media (max-width: 480px) {
  .content-header h2 {
    font-size: 1.5rem;
  }

  .content-header p {
    font-size: 16px; /* Smaller font size for mobile screens */
  }

  .section-container {
    padding: 10px;
  }
}

.project-detail-container {
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
  color: #fff;
  background-color: #121212;
}

.gif-container {
  display: flex;
  justify-content: space-between; /* Space out GIFs evenly */
  align-items: center; /* Align items vertically */
  margin-bottom: 20px;
}

.gif {
  width: 48%; /* Set a consistent width for both GIFs */
  height: auto; /* Maintain aspect ratio */
  border-radius: 5px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.video-container,
.videos-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.video-frame {
  margin: 0 10px;
}

.gallery-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.subtitle-container {
  text-align: center;
  margin: 20px 0;
}

.subtitle-container h3 {
  font-size: 1.8rem; /* Larger font size for subtitles */
  color: #ffcc00; /* Adjust color as needed */
  margin-bottom: 15px;
}
